import modal from './index.html';
import './index.css';
import { task, taskKeys, adultImagesPath } from './task';

class AdultTask {
  constructor() {
    this.currentTaskName = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTask = `${adultImagesPath}${this.currentTaskName}`;
    [this.currentLabel, this.currentTaskAnswers] = task[this.currentTaskName];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskLabel = template.querySelector('.adult-task-name');
    const taskImage = template.querySelector('.adult-task-image');
    taskLabel.textContent = this.currentLabel;
    taskImage.setAttribute('src', this.currentTask);

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const adultTaskAnswerButton = document.querySelector('.adult-task-button');
    const adultUserInput = document.querySelector('.adult-task-answer');

    return new Promise((resolve) => {
      adultTaskAnswerButton.onclick = () => {
        resolve(this.getResult() === adultUserInput.value.toLowerCase());
      };
      adultUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            adultTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default AdultTask;
