import modal from './index.html';
import './index.css';
import { task, taskKeys, listeningSoundsPath } from './task';

class ListeningTask {
  constructor() {
    this.currentTaskName = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTask = `${listeningSoundsPath}${this.currentTaskName}`;
    this.currentTaskAnswers = task[this.currentTaskName];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskSound = template.querySelector('.listening-task-sound');
    taskSound.setAttribute('src', this.currentTask);

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const listeningTaskAnswerButton = document.querySelector('.listening-task-button');
    const listeningUserInput = document.querySelector('.listening-task-answer');

    return new Promise((resolve) => {
      listeningTaskAnswerButton.onclick = () => {
        resolve(this.getResult() === listeningUserInput.value.toLowerCase());
      };
      listeningUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            listeningTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default ListeningTask;
