import modal from './index.html';
import './index.css';
import { task, taskKeys, transportImagesPath } from './task';

class TransportTask {
  constructor() {
    this.currentTaskName = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTask = `${transportImagesPath}${this.currentTaskName}`;
    this.currentTaskAnswers = task[this.currentTaskName];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskImage = template.querySelector('.transport-task-image');
    taskImage.setAttribute('src', this.currentTask);

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const transportTaskAnswerButton = document.querySelector('.transport-task-button');
    const transportUserInput = document.querySelector('.transport-task-answer');

    return new Promise((resolve) => {
      transportTaskAnswerButton.onclick = () => {
        resolve(this.getResult() === transportUserInput.value.toLowerCase());
      };
      transportUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            transportTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default TransportTask;
