import './index.css';
import road from './images/road.jpg';
import mars from './images/mars.jpg';
import desert from './images/desert.jpg';
import eveningDesert from './images/desert_1.jpg';
import mountain from './images/mountain.jpg';
import Hero from '../../components/hero';
import Monster from '../../components/monster';
import ModalDialog from '../../components/modal-dialog/index';
import tasks from '../../components/tasks';
import { rightAnswerAction, wrongAnswerAction } from './answerAction';

window.onload = () => {
  const backgroundsImages = [road, mars, desert, eveningDesert, mountain];
  const backgroundImage = backgroundsImages[Math.floor(Math.random() * backgroundsImages.length)];
  const body = document.querySelector('body');
  const actionButton = document.querySelector('.action-button');
  const parametrs = window.location.search.replace('?', '').split('&').map(parametr => decodeURIComponent(parametr.split('=')[1]));
  const hero = new Hero(parametrs[0].toUpperCase(), parametrs[1]);
  let monster = new Monster();
  const userName = document.querySelector('.health-user-name');
  const monsterName = document.querySelector('.health-monster-name');

  body.style.backgroundImage = `url(${backgroundImage})`;

  userName.textContent = hero.getName();
  monsterName.textContent = monster.getName();
  monster.createBody();
  actionButton.focus();

  actionButton.addEventListener('click', async () => {
    ModalDialog.showModalWindow();
    try {
      const [taskComplexity, hitType, hitDamage] = await ModalDialog.waitForPress();
      const taskArray = tasks[taskComplexity];
      const TaskType = taskArray[Math.floor(Math.random() * taskArray.length)];
      const task = new TaskType();
      const modalTask = document.querySelector('.modal-task');

      hero.setHitType(hitType);
      modalTask.innerHTML = task.showTask();
      if (TaskType.addEventListeners) {
        TaskType.addEventListeners();
      }
      modalTask.classList.toggle('modal-show');
      const taskUserInput = document.querySelector('.task-user-answer');
      if (taskUserInput) {
        taskUserInput.focus();
      }

      if (await task.waitForAnswer()) {
        rightAnswerAction(modalTask, hero, monster, hitDamage)
          .then((newMonster) => {
            monster = newMonster;
          });
      } else {
        wrongAnswerAction(modalTask, hero, monster, hitDamage);
      }
    } catch (e) {
      ModalDialog.showModalWindow();
    }
  });
};
