import modal from './index.html';
import './index.css';
import { task, taskKeys } from './task';

const removeEventListeners = (element, dragstartListener, dragoverListener, dropListener) => {
  element.removeEventListener('dragstart', dragstartListener);
  element.removeEventListener('dragover', dragoverListener);
  element.removeEventListener('drop', dropListener);
};

let dragItem;

const addDragStartListener = (event) => {
  if (event.target.tagName.toLowerCase() === 'span') {
    dragItem = event.target;
  }
};

const addDragOverListener = (event) => {
  event.preventDefault();
  return false;
};

const addDropListener = (event) => {
  if (event.target.tagName.toLowerCase() === 'span') {
    const letter = dragItem.textContent;
    const currentPosion = event.target;
    dragItem.textContent = event.target.textContent;
    currentPosion.textContent = letter;
  }
};

class MixedLettersTask {
  constructor() {
    this.currentTask = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTaskAnswer = task[this.currentTask];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskWrapper = template.querySelector('.mixed-letters-wrapper');
    const fragment = document.createDocumentFragment();
    this.currentTask.split('').forEach((letter) => {
      const span = document.createElement('span');
      span.setAttribute('draggable', 'true');
      span.setAttribute('dropzone', 'move');
      span.textContent = letter;
      fragment.appendChild(span);
    });
    taskWrapper.appendChild(fragment);

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswer;
  }

  waitForAnswer() {
    const dragElement = document.querySelector('.mixed-letters-wrapper');
    const mixedLettersAnswerButton = document.querySelector('.mixed-letters-task-button');

    return new Promise((resolve) => {
      mixedLettersAnswerButton.addEventListener('click', () => {
        removeEventListeners(dragElement, addDragStartListener,
          addDragOverListener, addDropListener);
        let answer = '';
        for (let i = 0; i < dragElement.children.length; i += 1) {
          answer += dragElement.children[i].textContent;
        }
        resolve(answer === this.getResult());
      }, { once: true });
    });
  }

  static addEventListeners() {
    const dragElement = document.querySelector('.mixed-letters-wrapper');

    dragElement.addEventListener('dragstart', addDragStartListener);
    dragElement.addEventListener('dragover', addDragOverListener);
    dragElement.addEventListener('drop', addDropListener);
  }
}

export default MixedLettersTask;
