import Hero from '../../components/hero';
import Monster from '../../components/monster';
import Score from '../score/Score';
import rightAnswer from './sounds/right-answer.mp3';
import wrongAnswer from './sounds/wrong-answer.mp3';

const modalOverlay = document.querySelector('.modal-overlay');
const actionButton = document.querySelector('.action-button');
const monsterName = document.querySelector('.health-monster-name');
const userHealth = document.querySelector('.health-user-ph');
const monsterHealth = document.querySelector('.health-monster-ph');
const round = document.querySelector('.round-number');

const closeModalTask = taskWindow => new Promise((resolve) => {
  setTimeout(() => {
    taskWindow.classList.remove('modal-show');
    modalOverlay.classList.remove('modal-show');
    taskWindow.classList.remove('modal-task-right-answer');
    taskWindow.classList.remove('modal-task-wrong-answer');
    actionButton.focus();
    resolve();
  }, 1500);
});

const addAnswerStyles = (taskWindow, answerClass, sound) => {
  taskWindow.classList.add(answerClass);

  const audio = document.createElement('audio');
  audio.setAttribute('src', sound);
  audio.setAttribute('autoplay', true);

  taskWindow.appendChild(audio);
};

const playerHitDamage = (player, health, hitDamage) => {
  const playerHealth = health;
  player.setPh(player.getPh() - hitDamage);
  playerHealth.style.width = `${player.getPh()}%`;
};

const createNewMonster = () => {
  const newMonster = new Monster();
  monsterName.textContent = newMonster.getName();
  newMonster.createBody();
  round.textContent = parseInt(round.textContent, 10) + 1;
  monsterHealth.style.width = '100%';
  return newMonster;
};

async function rightAnswerAction(taskWindow, hero, monster, hitDamage) {
  addAnswerStyles(taskWindow, 'modal-task-right-answer', rightAnswer);
  await closeModalTask(taskWindow);
  await hero.hit();
  playerHitDamage(monster, monsterHealth, hitDamage);
  if (monster.getPh() > 0) {
    await monster.takeHit();
    return monster;
  }
  await monster.monsterKill();
  return createNewMonster();
}

async function wrongAnswerAction(taskWindow, hero, monster, hitDamage) {
  addAnswerStyles(taskWindow, 'modal-task-wrong-answer', wrongAnswer);
  await closeModalTask(taskWindow);
  await monster.hit();
  await Hero.takeHit();
  playerHitDamage(hero, userHealth, hitDamage);
  if (hero.getPh() > 0) {
    await Hero.takeHit();
  } else {
    await Hero.userKill();
    Score.saveResult(hero.getName().toLowerCase());
  }
}

export { rightAnswerAction, wrongAnswerAction };
