import './index.css';
import chooseHitType from './chooseHitType';

const modalHitType = document.querySelector('.modal-hit-type');
const hitTypeWrapper = document.querySelector('.hit-type-wrapper');
const modalOverlay = document.querySelector('.modal-overlay');
const modalHitTypeClose = document.querySelector('.modal-hit-type-close');

class ModalDialog {
  static showModalWindow() {
    modalHitType.classList.toggle('modal-show');
    modalOverlay.classList.toggle('modal-show');
  }

  static waitForPress() {
    return new Promise((resolve, reject) => {
      hitTypeWrapper.onclick = (event) => {
        if (!event.target.classList.contains('hit-type-wrapper')) {
          modalHitType.classList.toggle('modal-show');
          resolve(chooseHitType(event.target));
        }
      };
      hitTypeWrapper.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            modalHitType.classList.toggle('modal-show');
            resolve(chooseHitType(event.target));
            break;
          default:
        }
      };
      modalHitTypeClose.onclick = () => {
        reject();
      };
    });
  }
}

export default ModalDialog;
