import modal from './index.html';
import './index.css';
import { task, taskKeys, animalImagesPath } from './task';

class AnimalTask {
  constructor() {
    this.currentTaskName = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTask = `${animalImagesPath}${this.currentTaskName}`;
    this.currentTaskAnswers = task[this.currentTaskName];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskImage = template.querySelector('.animal-task-image');
    taskImage.setAttribute('src', this.currentTask);

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const animalTaskAnswerButton = document.querySelector('.animal-task-button');
    const animalUserInput = document.querySelector('.animal-task-answer');

    return new Promise((resolve) => {
      animalTaskAnswerButton.onclick = () => {
        resolve(this.getResult() === animalUserInput.value.toLowerCase());
      };
      animalUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            animalTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default AnimalTask;
