import modal from './index.html';
import './index.css';
import { task, taskKeys, rebusImagesPath } from './task';

class RebusTask {
  constructor() {
    this.currentTaskName = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTask = `${rebusImagesPath}${this.currentTaskName}`;
    this.currentTaskAnswers = task[this.currentTaskName];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskImage = template.querySelector('.rebus-task-image');
    taskImage.setAttribute('src', this.currentTask);

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const rebusTaskAnswerButton = document.querySelector('.rebus-task-button');
    const rebusUserInput = document.querySelector('.rebus-task-answer');

    return new Promise((resolve) => {
      rebusTaskAnswerButton.onclick = () => {
        resolve(this.getResult() === rebusUserInput.value.toLowerCase());
      };
      rebusUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            rebusTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default RebusTask;
