import modal from './index.html';
import './index.css';
import { task, taskKeys } from './task';

const changeLetter = (target) => {
  if (target.tagName.toLowerCase() === 'span') {
    const currentLetter = document.querySelector('.find-task-letter-checked');
    const choosenLetter = target;

    currentLetter.classList.remove('find-task-letter-checked');
    choosenLetter.classList.add('find-task-letter-checked');
  }
};

const addFindErrorTaskChooseLetter = (event) => {
  changeLetter(event.target);
};

const addFindErrorTaskKeybordUse = (event) => {
  switch (event.code) {
    case 'Enter':
      changeLetter(event.target);
      break;
    default:
  }
};

const removeEventListeners = (wrapper, buttonListener, keybordListener) => {
  wrapper.removeEventListener('click', buttonListener);
  wrapper.removeEventListener('keyup', keybordListener);
};

class FindErrorTask {
  constructor() {
    this.currentTask = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTaskAnswer = task[this.currentTask];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskWrapper = template.querySelector('.find-error-wrapper');
    const fragment = document.createDocumentFragment();
    let letterNumber = 0;
    this.currentTask.split('').forEach((letter) => {
      const span = document.createElement('span');
      span.setAttribute('data-letter-number', `${letterNumber}`);
      span.setAttribute('tabindex', '0');
      span.textContent = letter;
      fragment.appendChild(span);
      letterNumber += 1;
    });
    taskWrapper.appendChild(fragment);
    taskWrapper.firstElementChild.classList.add('find-task-letter-checked');

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswer;
  }

  waitForAnswer() {
    const wordWrapper = document.querySelector('.find-error-wrapper');
    const findErrorAnswerButton = document.querySelector('.find-error-task-button');

    return new Promise((resolve) => {
      findErrorAnswerButton.addEventListener('click', () => {
        removeEventListeners(wordWrapper, addFindErrorTaskChooseLetter, addFindErrorTaskKeybordUse);
        const answer = document.querySelector('.find-task-letter-checked').getAttribute('data-letter-number');
        resolve(answer === this.getResult());
      }, { once: true });
    });
  }

  static addEventListeners() {
    const wordWrapper = document.querySelector('.find-error-wrapper');
    wordWrapper.addEventListener('click', addFindErrorTaskChooseLetter);
    wordWrapper.addEventListener('keyup', addFindErrorTaskKeybordUse);
  }
}

export default FindErrorTask;
