import modal from './index.html';
import './index.css';
import { task, taskKeys, colorImagesPath } from './task';

class ColorTask {
  constructor() {
    this.currentTaskName = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTask = `${colorImagesPath}${this.currentTaskName}`;
    this.currentTaskAnswers = task[this.currentTaskName];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskImage = template.querySelector('.color-task-image');
    taskImage.setAttribute('src', this.currentTask);

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const colorTaskAnswerButton = document.querySelector('.color-task-button');
    const colorUserInput = document.querySelector('.color-task-answer');

    return new Promise((resolve) => {
      colorTaskAnswerButton.onclick = () => {
        resolve(this.getResult() === colorUserInput.value.toLowerCase());
      };
      colorUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            colorTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default ColorTask;
