import modal from './index.html';
import './index.css';
import { task, taskKeys } from './task';

class RiddleTask {
  constructor() {
    this.currentTask = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTaskAnswers = task[this.currentTask];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskName = template.querySelector('.riddle-task-label');
    taskName.textContent = this.currentTask;

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const riddlesTaskAnswerButton = document.querySelector('.riddle-task-button');
    const riddlesUserInput = document.querySelector('.riddle-task-answer');

    return new Promise((resolve) => {
      riddlesTaskAnswerButton.onclick = () => {
        resolve(this.getResult().indexOf(riddlesUserInput.value.toLowerCase()) >= 0);
      };
      riddlesUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            riddlesTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default RiddleTask;
