import './index.css';
import './images/sprites/ironman/firstType.png';
import './images/sprites/ironman/secondType.png';
import './images/sprites/spiderman/firstType.png';
import './images/sprites/spiderman/secondType.png';
import './images/sprites/blackpanther/firstType.png';
import './images/sprites/blackpanther/secondType.png';
import './images/sprites/hulk/firstType.png';
import './images/sprites/hulk/secondType.png';

const spritePath = './src/components/hero/images/sprites';
const takeHitSound = document.querySelector('.hero-take-hit-sound');
const makeHitSound = document.querySelector('.hero-make-hit-sound');
const gameOverSound = document.querySelector('.game-over-sound');

class Hero {
  constructor(name, type) {
    const battleUserHero = document.querySelector('.battle-user-hero');
    this.name = name;
    this.type = type;
    this.Ph = 100;
    this.sprites = {
      firstType: `${spritePath}/${type.toLowerCase()}/firstType.png`,
      secondType: `${spritePath}/${type.toLowerCase()}/secondType.png`,
    };
    this.hitType = this.sprites.firstType;
    this.hitTypeString = 'firstType';
    battleUserHero.style.background = `url(${this.hitType})`;
  }

  getName() {
    return this.name;
  }

  getType() {
    return this.type;
  }

  getPh() {
    return this.Ph;
  }

  setPh(Ph) {
    this.Ph = Ph;
  }

  getHitType() {
    return this.hitType;
  }

  setHitType(hitType) {
    const battleUserHero = document.querySelector('.battle-user-hero');
    this.hitType = `${this.sprites[hitType]}`;
    this.hitTypeString = hitType;
    battleUserHero.style.background = `url(${this.hitType})`;
  }

  hit() {
    const battleUserHero = document.querySelector('.battle-user-hero');
    makeHitSound.play();
    battleUserHero.classList.add(`battle-${this.type.toLowerCase()}-${this.hitTypeString}-hit`);
    return new Promise((resolve) => {
      setTimeout(() => {
        battleUserHero.classList.remove(`battle-${this.type.toLowerCase()}-${this.hitTypeString}-hit`);
        resolve();
      }, 1400);
    });
  }

  static takeHit() {
    const battleUserHero = document.querySelector('.battle-user-hero');
    takeHitSound.play();
    battleUserHero.classList.add('battle-hero-shaked');
    return new Promise((resolve) => {
      setTimeout(() => {
        battleUserHero.classList.remove('battle-hero-shaked');
        resolve();
      }, 600);
    });
  }

  static userKill() {
    const battleUserHero = document.querySelector('.battle-user-hero');
    gameOverSound.play();
    battleUserHero.classList.add('battle-hero-killed');
    return new Promise((resolve) => {
      setTimeout(() => {
        battleUserHero.classList.remove('battle-hero-killed');
        resolve();
      }, 1500);
    });
  }
}

export default Hero;
