import modal from './index.html';
import './index.css';

class MathTask {
  constructor() {
    this.maxFirstValue = 30;
    this.maxSecondValue = 15;
    this.operators = ['+', '-', '*', '/'];
    this.firstOperand = Math.floor(Math.random() * this.maxFirstValue);
    this.secondOperand = Math.floor(Math.random() * this.maxSecondValue);
    this.operator = this.operators[Math.floor(Math.random() * this.operators.length)];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const firstOperand = template.querySelector('.math-task-first-operand');
    const operator = template.querySelector('.math-task-operator');
    const secondOperand = template.querySelector('.math-task-second-operand');

    firstOperand.textContent = this.firstOperand;
    operator.textContent = this.operator;
    secondOperand.textContent = this.secondOperand;

    return template.innerHTML;
  }

  getResult() {
    switch (this.operator) {
      case '+':
        return this.firstOperand + this.secondOperand;
      case '-':
        return this.firstOperand - this.secondOperand;
      case '*':
        return this.firstOperand * this.secondOperand;
      case '/':
        return Math.floor(this.firstOperand / this.secondOperand);
      default:
        return 0;
    }
  }

  waitForAnswer() {
    const mathTaskAnswerButton = document.querySelector('.math-task-button');
    const mathUserInput = document.querySelector('.math-task-answer');

    return new Promise((resolve) => {
      mathTaskAnswerButton.onclick = () => {
        resolve(parseInt(mathUserInput.value, 10) === this.getResult());
      };
      mathUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            mathTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default MathTask;
