class Score {
  static saveResult(name) {
    const result = document.querySelector('.round-number').textContent;
    fetch('/scoredb',
      {
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify({
          userName: name,
          score: result,
        }),
      })
      .then(() => {
        window.location.href = 'score.html';
      });
  }

  static showResult() {
    const tableBody = document.querySelector('.score-table tbody');
    const template = document.querySelector('.score-table-row');

    fetch('/scoredb')
      .then(response => response.json())
      .then((result) => {
        result.sort((a, b) => b.score - a.score);
        result.forEach((user, index) => {
          const row = template.content.cloneNode(true);
          const td = row.querySelectorAll('td');
          const { userName, score } = user;
          td[0].textContent = index + 1;
          td[1].textContent = userName;
          td[2].textContent = score;
          tableBody.appendChild(row);
        });
      });
  }
}

export default Score;
