import modal from './index.html';
import './index.css';
import { task, taskKeys } from './task';

class GrammarTask {
  constructor() {
    this.currentTask = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTaskAnswers = task[this.currentTask];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskName = template.querySelector('.grammar-task-label');
    taskName.textContent = this.currentTask;

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const grammarTaskAnswerButton = document.querySelector('.grammar-task-button');
    const grammarUserInput = document.querySelector('.grammar-task-answer');

    return new Promise((resolve) => {
      grammarTaskAnswerButton.onclick = () => {
        resolve(this.getResult() === grammarUserInput.value.toLowerCase());
      };
      grammarUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            grammarTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default GrammarTask;
