import modal from './index.html';
import './index.css';
import { task, taskKeys } from './task';

class TranslateTask {
  constructor() {
    this.currentTask = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    this.currentTaskAnswers = task[this.currentTask];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskName = template.querySelector('.translate-task-label');
    taskName.textContent = this.currentTask;

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswers;
  }

  waitForAnswer() {
    const translateTaskAnswerButton = document.querySelector('.translate-task-button');
    const translateUserInput = document.querySelector('.translate-task-answer');

    return new Promise((resolve) => {
      translateTaskAnswerButton.onclick = () => {
        resolve(this.getResult().indexOf(translateUserInput.value.toLowerCase()) >= 0);
      };
      translateUserInput.onkeyup = (event) => {
        switch (event.code) {
          case 'Enter':
            event.preventDefault();
            translateTaskAnswerButton.click();
            break;
          default:
        }
      };
    });
  }
}

export default TranslateTask;
