const chooseHitType = (target) => {
  let type = target;
  while (!type.classList.contains('hit-type')) {
    type = type.parentElement;
  }
  const hitTypeIndex = type.getAttribute('data-hitIndex');
  const hitType = type.getAttribute('data-hitType');
  const hitDamage = type.getAttribute('data-hitDamage');

  return [hitTypeIndex, hitType, hitDamage];
};

export default chooseHitType;
