import modal from './index.html';
import './index.css';
import { task, taskKeys } from './task';

const changeLetter = (target) => {
  if (target.tagName.toLowerCase() === 'span') {
    const currentLetter = document.querySelector('.question-task-letter-checked');
    const choosenLetter = target;

    currentLetter.classList.remove('question-task-letter-checked');
    choosenLetter.classList.add('question-task-letter-checked');
  }
};

const removeEventListeners = (wrapper, buttonListener, keybordListener) => {
  wrapper.removeEventListener('click', buttonListener);
  wrapper.removeEventListener('keyup', keybordListener);
};

const addQuestionTaskChooseLetter = (event) => {
  changeLetter(event.target);
};

const addQuestionTaskKeybordUse = (event) => {
  switch (event.code) {
    case 'Enter':
      changeLetter(event.target);
      break;
    default:
  }
};

class QuestionTask {
  constructor() {
    this.currentTask = taskKeys[Math.floor(Math.random() * taskKeys.length)];
    [this.currentTaskVariants, this.currentTaskAnswer] = task[this.currentTask];
  }

  showTask() {
    const template = document.createElement('div');
    template.innerHTML = modal;

    const taskLabel = template.querySelector('.question-task-name');
    const taskWrapper = template.querySelector('.question-task-wrapper');
    const fragment = document.createDocumentFragment();
    taskLabel.textContent = this.currentTask;
    let letterNumber = 0;
    this.currentTaskVariants.forEach((answer) => {
      const span = document.createElement('span');
      span.setAttribute('data-letter-number', `${letterNumber}`);
      span.setAttribute('tabindex', '0');
      span.textContent = answer;
      fragment.appendChild(span);
      letterNumber += 1;
    });
    taskWrapper.appendChild(fragment);
    taskWrapper.firstElementChild.classList.add('question-task-letter-checked');

    return template.innerHTML;
  }

  getResult() {
    return this.currentTaskAnswer;
  }

  waitForAnswer() {
    const wordWrapper = document.querySelector('.question-task-wrapper');
    const questionAnswerButton = document.querySelector('.question-task-button');

    return new Promise((resolve) => {
      questionAnswerButton.addEventListener('click', () => {
        removeEventListeners(wordWrapper, addQuestionTaskChooseLetter, addQuestionTaskKeybordUse);
        const answer = document.querySelector('.question-task-letter-checked').getAttribute('data-letter-number');
        resolve(answer === this.getResult());
      }, { once: true });
    });
  }

  static addEventListeners() {
    const wordWrapper = document.querySelector('.question-task-wrapper');
    wordWrapper.addEventListener('click', addQuestionTaskChooseLetter);
    wordWrapper.addEventListener('keyup', addQuestionTaskKeybordUse);
  }
}

export default QuestionTask;
