import './index.css';
import {
  nameAdjqctives, nameNouns, names, heads, bodies, leftHands, leftLegs, rightHands, rightLegs,
} from './monster';

const createName = () => {
  const adj = nameAdjqctives[Math.floor(Math.random() * nameAdjqctives.length)];
  const noun = nameNouns[Math.floor(Math.random() * nameNouns.length)];
  const name = names[Math.floor(Math.random() * names.length)];
  return `${adj.toUpperCase()} ${noun.toUpperCase()} ${name.toUpperCase()}`;
};
const getRandomPiece = array => array[Math.floor(Math.random() * array.length)];

class Monster {
  constructor() {
    this.name = createName();
    this.Ph = 100;
    this.head = document.querySelector('.monster-head');
    this.body = document.querySelector('.monster-body');
    this.leftHand = document.querySelector('.monster-left-hand');
    this.rightHand = document.querySelector('.monster-right-hand');
    this.leftLeg = document.querySelector('.monster-left-leg');
    this.rightLeg = document.querySelector('.monster-right-leg');
    this.monsterTakeHitSound = document.querySelector('.monster-take-hit-sound');
    this.vulkanSound = document.querySelector('.vulkan-sound');
    this.nextMonsterSound = document.querySelector('.next-please-sound');
  }

  getName() {
    return this.name;
  }

  getPh() {
    return this.Ph;
  }

  setPh(Ph) {
    this.Ph = Ph;
  }

  createBody() {
    const monsterHead = getRandomPiece(heads);
    const monsterBody = getRandomPiece(bodies);
    const monsterLeftHand = getRandomPiece(leftHands);
    const monsterLeftLeg = getRandomPiece(leftLegs);
    const monsterRightHand = getRandomPiece(rightHands);
    const monsterRightLeg = getRandomPiece(rightLegs);

    this.head.style.background = `url(${monsterHead})`;
    this.body.style.background = `url(${monsterBody})`;
    this.leftHand.style.background = `url(${monsterLeftHand})`;
    this.leftLeg.style.background = `url(${monsterLeftLeg})`;
    this.rightHand.style.background = `url(${monsterRightHand})`;
    this.rightLeg.style.background = `url(${monsterRightLeg})`;
  }

  makeMonsterDamage(type) {
    this.head.classList.add(`battle-monster-head-${type}`);
    this.body.classList.add(`battle-monster-body-${type}`);
    this.leftHand.classList.add(`battle-monster-left-hand-${type}`);
    this.leftLeg.classList.add(`battle-monster-left-leg-${type}`);
    this.rightHand.classList.add(`battle-monster-right-hand-${type}`);
    this.rightLeg.classList.add(`battle-monster-right-leg-${type}`);
  }

  hit() {
    this.vulkanSound.play();
    const battleMonsterBody = document.querySelector('body');
    battleMonsterBody.classList.add('battle-monster-hit');
    return new Promise((resolve) => {
      setTimeout(() => {
        battleMonsterBody.classList.remove('battle-monster-hit');
        resolve();
      }, 1500);
    });
  }

  takeHit() {
    this.monsterTakeHitSound.play();
    this.makeMonsterDamage('shaked', 600);
    return new Promise((resolve) => {
      setTimeout(() => {
        this.head.classList.remove('battle-monster-head-shaked');
        this.body.classList.remove('battle-monster-body-shaked');
        this.leftHand.classList.remove('battle-monster-left-hand-shaked');
        this.leftLeg.classList.remove('battle-monster-left-leg-shaked');
        this.rightHand.classList.remove('battle-monster-right-hand-shaked');
        this.rightLeg.classList.remove('battle-monster-right-leg-shaked');
        resolve();
      }, 600);
    });
  }

  monsterKill() {
    this.monsterTakeHitSound.play();
    this.makeMonsterDamage('kill');
    return new Promise((resolve) => {
      setTimeout(() => {
        this.head.classList.remove('battle-monster-head-kill');
        this.body.classList.remove('battle-monster-body-kill');
        this.leftHand.classList.remove('battle-monster-left-hand-kill');
        this.leftLeg.classList.remove('battle-monster-left-leg-kill');
        this.rightHand.classList.remove('battle-monster-right-hand-kill');
        this.rightLeg.classList.remove('battle-monster-right-leg-kill');
        this.nextMonsterSound.play();
        resolve();
      }, 1000);
    });
  }
}

export default Monster;
